// //-- Ejercicios entregables ****************************************
// // 1. Array operations
// // 1.1.- Funci√≥n Head. Implementa una funci√≥n head (inmutable), tal que, dado un array como entrada extraiga y devuelva su primer elemento. Utiliza destructuring.

const family = ["Juan Carlos", "Laura", "Chloe"];

const head = (arr) =>{ 
  const [first] = arr;
  return first;
};

console.log(`1.1.- Con la funci√≥n head extraemos el primer elemento del array:`);
console.log(head(family));

//  // 1.2.- Funci√≥n Tail. Implementa una funci√≥n tail (inmutable), tal que, dado un array como entrada devuelta todos menos el primer elemento. Utiliza rest operator.

const tail = ([,...arr]) => {
    
    return arr;
};

console.log(`+--------------------------------------------------------+`);
console.log(`1.2.- Con la funci√≥n tail extraemos todos los elementos del array menos el primero:`);
console.log(tail(family));

// // 1.3.- Funci√≥n Init. Implementa una funci√≥n init(inmutable), tal que, dado un array como entrada devuelva todos los elementos menos el √∫ltimo. Utiliza los m√©todos que ofrece Array.prototype

const init = (arr) => {
    const [...newArray] = arr;
    newArray.pop();
    return newArray;
};

console.log(`+--------------------------------------------------------+`);
console.log(`1.3.- Con la funci√≥n init extraemos todos los elementos del array menos el √∫ltimo:`);
console.log(init(family));

// // 1.4.- Funci√≥n Last. Implementa una funci√≥n last (inmutable), tal que, dado un array como entrada devuelva el √∫ltimo elemento.

const last = (arr) => {
    
    const [...newArray] = arr;
    
    return newArray.pop();
};

console.log(`+--------------------------------------------------------+`);
console.log(`1.4.- Con la funci√≥n last extraemos el √∫ltimo elemento del array:`);
console.log(last(family));

// //EJERCICIO 2 -- CONCAT
// // 2.- Implementa una funci√≥n concat (inmutable) tal que, dados 2 arrays como entrada, devuelva la concatenaci√≥n de ambos. Utiliza rest / spread operators.

const family2 = ["Juan", "Teresa", "Mari Carmen"]

const concat = (a, b) => {
    
    return [...a, ...b];
    
};

console.log(`+--------------------------------------------------------+`);
console.log(`2.- Con la funci√≥n concat concatenamos dos arrays en uno:`);
console.log(concat(family, family2));
console.log(`Comprobamos que el array original sigue inmutable:`)
console.log(family);

// //EJERCICIO 3 -- CLONE MERGE
// // 3.1.- Clone.- mplementa una funci√≥n clone que, a partir de un objeto de entrada source devuelva un nuevo objeto con las propiedades de source.

const person = {
    name: "Juan Carlos",
    surname: "Fuentes",
    age: "49",
};

function clone(source) {
    
    return {...source}
};

const newPerson = clone(person);

console.log(`+--------------------------------------------------------+`);
console.log(`3.1.- Vemos que hemos clonado el objeto:`);
console.log(newPerson);
console.log(`Y comprobamos que cada uno apunta a una referencia distinta, por eso no son iguales:`);
console.log(newPerson === person);

// // 3.2.- Merge.- Implementa una funci√≥n merge que, dados dos objetos de entrada source y target, devuelva un nuevo objeto con todas las propiedades de target y de source, y en caso de propiedades con el mismo nombre, source sobreescribe a target

const a = { name: "Maria", surname: "Iba√±ez", country: "SPA" };
const b = { name: "Luisa", age: 31, married: true };

function merge(source, target) {
    return {...target, ...source}
};

console.log(`+--------------------------------------------------------+`);
console.log(`3.2.- Vemos un solo objeto en el que el source sobreescribe los elementos repetidos sobre el target:`);
console.log(merge(a, b));

