//     }
// }
        
const books: Book[] = [
    new Book('Harry Potter y la piedra filosofal', true),
    new Book('Canci√≥n de hielo y fuego', false),
    new Book('Devastaci√≥n', true),
];

function isBookRead(books: Book[], titleToTest: string): string | boolean{
    let bookReaded = books.filter(b => b.title === titleToTest);
    
    if (bookReaded.length == 0)
        return false;
    else 
        return bookReaded[0].isRead ? `El libro ${bookReaded[0].title} ha sido le√≠do` : `El libro ${bookReaded[0].title} no se ha le√≠do`;
    
}

